<template>
    <span class="Checkbox" :class="computedClass">
        <font-awesome-icon class="check" icon="check" />
    </span>
</template>

<script>
    export default {
        name: 'Checkbox',
        props: {
            disabled: {
                type: Boolean,
                default: false,
            },
            checked: {
                type: Boolean,
                default: false,
            },
        },
        computed: {
            computedClass() {
                return {
                    checked: this.checked,
                    disabled: this.disabled,
                };
            },
        },
        methods: {
            click() {
                this.$emit('click');
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';
    @import "@/assets/scss/bulma/projectBulma.scss";

    .Checkbox {
        cursor: pointer;
        user-select: none;
        padding: 1px 2px;
        background-color: #eee;
        border: solid 1px $primary;
        border-radius: 3px;
        color: #eee;
        font-size: 0.7em;
        transition: 0.1s ease-out background-color;

        &.disabled {
            pointer-events: none;
            opacity: 0.2;
        }

        .check {
            opacity: 0;
            transition: 0.2s ease-out opacity;
        }

        &.checked {
            background-color: $primary;

            .check {
                opacity: 1;
            }
        }
    }
</style>
